import React from "react";
import { Link, withRouter } from "react-router-dom";

import { showCookies } from "./Cookies";
import { setStorageItem } from "../config/config";

import doc1 from "../documents/gdpr.pdf";
import doc2 from "../documents/obchodne-podmienky.pdf";

import "../styles/footer.css";

function Footer(props) {

    function redirect(type, category) {
        if (props.location.pathname.includes("e-shop")) {
            props.history.push("/");
            props.history.push({ pathname: "/e-shop", type: type, category: category})
        } else {
            props.history.push({ pathname: "/e-shop", type: type, category: category})
        }
    }

    return(
        <div id="footer">
            {/*
            <div className="button-panel">
                <div className="button-filled">Kontaktuje nás</div>
                <div className="button-outline">Ponuka olejov</div>
            </div>
            */}

            <div className="wrapper">
                <div className="logo-panel">
                    <Link to="/"><img className="logo" src={require("../assets/logo.png")} /></Link>
                </div>

                <div className="top-panel">
                    <div className="column">
                        <div className="title">Navigácia</div>
                        <Link className="info" to="/e-shop">E-shop</Link>
                        <Link className="info" to="/aromavzdelavanie">Aromavzdelávanie</Link>
                        <Link className="info" to="/podnikanie">Podnikanie</Link>
                        <Link className="info" to="/novinky">Novinky</Link>
                        <Link className="info" to="/blog">Blog</Link>
                        <Link className="info" to="/o-nas">O nás</Link>
                        <Link className="info" to="/kontakt">Kontakt</Link>
                    </div>

                    <div className="column">
                        <div className="title">Produkty</div>
                        <div className="info" onClick={() => redirect(0, 0)}>Esenciálne oleje</div>
                        <div className="info" onClick={() => redirect(1, 0)}>Produkty osobnej hygieny</div>
                        <div className="info" onClick={() => redirect(2, 0)}>Výživové doplnky</div>
                        <div className="info" onClick={() => redirect(3, 0)}>Difúzery</div>
                        <div className="info" onClick={() => redirect(4, 0)}>Príslušenstvo</div>
                    </div>

                    <div className="column">
                        <div className="title" style={{ visibility: "hidden" }}>Produkty</div>
                        <div className="info" onClick={() => redirect(5, 0)}>Literatúra</div>
                        <div className="info" onClick={() => redirect(6, 0)}>Balíky a kolekcie</div>
                        <div className="info" onClick={() => redirect(7, 0)}>Špeciálne ponuky</div>
                        <div className="info" onClick={() => redirect(8, 0)}>Produktové rady doTERRA</div>
                        <div className="info" onClick={() => redirect(9, 0)}>Produkty TerraMia</div>
                    </div>

                    <div className="column">
                        <div className="title">Súkromie</div>
                        <a className="info" href={doc1} target="_blank">Ochrana osobných údajov</a>
                        <a className="info" href={doc2} target="_blank">Obchodné podmienky</a>
                        <div className="info" style={{ cursor: "pointer" }} onClick={() => {
                            setStorageItem("cookies", "disabled");
                            showCookies();
                        }}>Cookies</div>

                        <br />

                        <Link className="button-filled" to="/kontakt">Napíšte nám</Link>
                    </div>
                </div>

                <div className="bottom-panel">
                    <div className="text">© 2020 terramia.sk | Designed and created by <a href="https://advision.sk" style={{ color: "#A161B3", fontWeight: "bold", textDecoration: "none" }}>AdVision Digital Marketing</a></div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(Footer);